.SkillsContainer {
    height: 100%;
    width: 80vw;

    /* background-color: #E8E6EA; */
    background-image: linear-gradient(#073F5A, #E8E6EA); 

    display: flex;
    flex-direction: column;
    align-items: center;
}

.SkillsCard {
    /* height: 95vh; */
    width: 90%;
    /* background-color: #073F5A; */
    border: 5px solid #1F1C17;
    border-radius: 25px;


    display: grid;
    grid-template-columns: repeat(4, 1fr);  
    row-gap: 20px;
    padding: 15px 15px;
    margin: 25px 0;
}

.FlashcardBack {
    display: flex;
    flex-direction: column;

}

.FlashcardLogo {
    filter: drop-shadow(5px 5px 3px #1F1C17);
}

.FlashcardBody {
    font-size: 0.75rem;
}

.FlashcardListBullet {
    padding: 0 0 0 5px;
}

.FlashcardImages {
    display: grid;
    grid-template-columns: repeat(4, 1fr);  
    row-gap: 1.5px;
}