.HomeContainer {
    height: 100vh;
    width: 80vw;

    /* background-color: #E8E6EA; */
    background-image: linear-gradient(#E8E6EA, #55725D); 

    display: flex;
    flex-direction: column;
    align-items: center;
}

.HomeCard {
    width: 90%;
    background-color: #073F5A;
    border: 7.5px solid #1F1C17;
    border-radius: 25px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 10px 0;
}

.ImageContainer {
    height: 225px;
    width: 225px;

    border: 10px solid #E8E6EA;
    border-radius: 100%;

    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;

    margin: 50px 0px 25px 0px;
}

.NameContainer {
    margin: 15px;
}

.FirstName {
    font-size: 2.75rem;
    text-shadow: 1px 1px 2px #E8E6EA;
}

.LastName {
    font-size: 2.75rem;
    color: #8CB27F;
    text-shadow: 2px 2px 3px black;
}

.ButtonContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    margin: 33px;
}

.HomeButton {
    width: 165px;
    height: 40px;

    background-color: #8CB27F;
    color: #1F1C17;
    border: 2.5px solid #1F1C17;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0px 15px;
}

.HomeButton:hover{
    background-color: #55725d;
}

.DownArrowContainer {
    margin: 10px;
}