.AboutMeContainer {
    height: 100vh;
    width: 80vw;

    /* background-color: #E8E6EA; */
    background-image: linear-gradient(#55725D, #073F5A); 

    display: flex;
    flex-direction: column;
    align-items: center;
}

.AboutMeCard {
    height: 90vh;
    width: 90%;
    background-color: #E6E2EA;
    border: 5px solid #1F1C17;
    border-radius: 25px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: left;

    margin: 10px 0;
}